.head {
  display: flex;
  justify-content: flex-end;
}

.subhead {
  text-align: center;
  padding: 5%;
}

.counterhead {
  text-align: center;
}

.countervalue {
  font-size: 2em;
}

.mainCounter {
  display: flex;
  justify-content: center;
}

.counter {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  text-align: center;
  width: 70%;
}

#root>.lit {
  height: 99.6vh;
}

#root>.dark {
  height: 99.6vh;
}

.lit {
  background-color: white;
  color: black;
  border: 1px solid black;
}

.dark {
  background-color: black;
  color: white;
  border: 1px solid white;
}

.button {
  margin: 1%;
  margin-right: 3%;
  height: 60px;
  width: 60px;
  border-radius: 10px;
  box-shadow: 1px 1px 2px 1px mediumturquoise;
  cursor: pointer;
}


.countButton {
  min-width: 20%;
  width: auto;
  overflow-wrap: break-word;
  height: auto;
  padding: 2%;
  font-size: 1.4em;
  font-weight: 600;
  border-radius: 15px;
  box-shadow: 1px 1px 2px 2px mediumturquoise;
  cursor: pointer;
}

.counter>.lit {
  background-color: black;
  color: white;
}

.counter>.dark {
  background-color: white;
  color: black;
}

.resetButton {
  min-width: 12%;
  width: auto;
  word-break: break-all;
  height: auto;
  padding: 1%;
  font-size: 1.4em;
  font-weight: 600;
  border-radius: 15px;
  box-shadow: 1px 1px 2px 2px mediumturquoise;
  cursor: pointer;
}

.subhead>.lit {
  background-color: black;
  color: white;
}

.subhead>.dark {
  background-color: white;
  color: black;
}

@media screen and (max-width:500px) {

  .subhead {
    font-size: 0.7em;
  }

  .counterhead {
    font-size: 1.4em;
  }

  .counter {
    width: 94%;
  }

  .countervalue {
    font-size: 1.2em;
  }

  .countButton {
    font-size: 0.9em;
  }

  .resetButton {
    font-size: 1em;
  }
}